.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#footer{
  background-color: #282c34;
  position: fixed;
  bottom:0;
  z-index: 1;
}

#cr-section{
  justify-content: center;
}

.infos-subsection{
  min-height: 5rem;
  max-height: 20rem;
}

#admin-image{
  padding-top: 10%;
  max-height: 10rem;
}

.contact span, small{
  color: #ffffff;
  display: block;
  text-align: left;
  border-color: white solid 2px;
}

.infos-subsection h5{
  padding-top: 10%;
  color: #ffffff;
}

.main{
  margin-top: 5%;
}

#inputDiv{
  margin-top: 5%;
}